import React from 'react';
import { Link } from 'gatsby';
import Layout from '../components/layout';
import ThumbnailFirst from '../images/thumbnail_first-640x350.jpg';
import ThumbnailPersonal from '../images/thumbnail_personal-640x350.jpg';

export default function Course() {
  return (
    <Layout page='course' subtitle='プラン・料金' subtitleEn='PRICE'>
      <main className='l-main'>
        <div className='wp-wrap-post'>
          <div data-row-pc='3' data-row-sp='1'>
            {[
              {
                link: '/course/first/',
                image: ThumbnailFirst,
                label: 'はじめての方へ'
              },
              {
                link: '/course/personal/',
                image: ThumbnailPersonal,
                label: 'パーソナルトレーニング料金'
              }
            ].map(({ link, image, label }) => (
              <Link key={link} to={link} className='c-panel c-panel--col'>
                <div className='c-panel__img'>
                  <figure>
                    <img src={image} alt={label} />
                  </figure>
                </div>
                <div className='c-panel__detail'>
                  <div className='c-panel__title'>{label}</div>
                </div>
              </Link>
            ))}
          </div>
        </div>
      </main>
    </Layout>
  );
}
